@charset "UTF-8";
/*
@media only screen — только для экранов монитора;
min-width — условие «истина», когда ширина окна браузера больше установленного значения;
max-width — условие «истина», когда ширина окна браузера наоборот меньше установленного значения;
min-device-width — условие «истина», когда размер экрана устройства больше установленного значения;
max-device-width — условие «истина», когда размер экрана устройства меньше установленного значения;
*/
.specialty {
  width: 30%;
  margin: 1%; }

.allSpecializations {
  column-count: 3; }

@media only screen and (min-device-width: 1px) and (max-device-width: 399px) {
  .specialty {
    width: 99%;
    text-align: center; }
  .allSpecializations {
    column-count: 1; } }

@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min-device-pixel-ratio: 1.5) {
  .specialty {
    width: 99%;
    text-align: center; }
  .allSpecializations {
    column-count: 1; } }

@media only screen and (min-device-width: 400px) and (max-device-width: 480px) {
  .specialty {
    width: 45%;
    margin: 2%; }
  .allSpecializations {
    column-count: 2; } }

@media only screen and (min-width: 321px) {
  .specialty {
    width: 99%;
    text-align: center; }
  .allSpecializations {
    column-count: 1; } }

@media only screen and (max-width: 320px) {
  .specialty {
    width: 99%;
    text-align: center; }
  .allSpecializations {
    column-count: 1; } }

@media only screen and (min-device-width: 668px) and (max-device-width: 1024px) {
  .specialty {
    width: 45%;
    margin: 2%;
    text-align: left; }
  .allSpecializations {
    column-count: 2; } }

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) {
  .specialty {
    width: 45%;
    margin: 2%;
    text-align: left; }
  .allSpecializations {
    column-count: 2; } }

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait) {
  .specialty {
    width: 45%;
    margin: 2%;
    text-align: left; }
  .allSpecializations {
    column-count: 2; } }

@media only screen and (min-width: 1025px) {
  .specialty {
    width: 30%;
    margin: 1%;
    text-align: left; }
  .allSpecializations {
    column-count: 3; } }

@media only screen and (min-width: 1824px) {
  .specialty {
    width: 20%;
    margin: 2%;
    text-align: left; }
  .allSpecializations {
    column-count: 4; } }

@media screen and (min-width: 768px) {
  .categories {
    float: right; } }

.tabsPanel {
  position: absolute;
  border: 2px solid #32943f;
  background-color: #FFFFFF;
  width: 100%; }

.tabsPanel_tabsPanelHidden {
  border-width: 0;
  padding-bottom: 0;
  min-height: 0;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  z-index: 1;
  transform: translate3d(0, -30px, 0);
  transition: visibility 0s ease-in .15s, opacity .15s ease-in, transform .15s ease-in; }

.tabsPanel_tabsPanelOpen {
  min-height: 600px;
  position: relative;
  z-index: 1;
  visibility: visible;
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition: visibility 0s ease-out, opacity .25s ease-out, transform .25s ease-out; }

.container {
  position: relative;
  padding-left: 25px;
  padding-right: 25px; }

.categories {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 100%;
  margin-left: -9px; }

.tab_tabHover {
  border-radius: 4px;
  background-color: #f4f4f4; }

.tab {
  display: flex;
  width: 90%;
  cursor: pointer;
  margin-bottom: 1px;
  height: 36px;
  position: relative;
  align-items: center;
  justify-content: space-between;
  color: #333;
  user-select: none; }
  .tab:first-child {
    margin-top: 15px; }
  .tab:hover {
    border-radius: 4px;
    background-color: #f4f4f4; }
  .tab a {
    height: 100%;
    width: 100%;
    padding-left: 11px;
    color: #222; }
  .tab a:hover {
    text-decoration: none; }
  .tab .arrow {
    float: right;
    margin-right: 10px;
    color: rgba(0, 0, 0, 0.3); }

.specialtySpecialization {
  display: flex;
  flex-flow: row wrap;
  margin-top: 20px;
  width: 100%; }

.currentDirectionName {
  margin-top: 32px; }

.currentDirectionName a {
  height: 36px;
  font-weight: 700;
  font-size: 28px;
  line-height: 1.29;
  color: #222; }

.currentDirectionName a:hover {
  color: red;
  text-decoration: none; }

.arrowBack {
  margin: 0 20px 10px 0;
  cursor: pointer;
  font-weight: 700;
  font-size: 28px; }

.arrowBack:hover {
  color: red; }

.specialty {
  display: flex;
  flex-direction: column;
  text-overflow: ellipsis; }

.specialtyName a {
  height: 20px;
  cursor: pointer;
  color: #000;
  font-weight: 700; }

.specialtyName a:hover {
  color: red;
  text-decoration: none; }

.specializations {
  display: flex;
  flex-direction: column; }

.oneLine {
  margin: 0 10px 5px; }

.specialization {
  padding-right: 5px;
  color: #888;
  cursor: pointer;
  transition: color .25s ease-out, border-color .25s ease-out; }

.specialization:hover {
  color: red;
  text-decoration: none; }
